iv>\n                </div>\n                <div\n                    v-if=\"\n                        ceremony.menDress ||\n                        ceremony.ladiesDress ||\n                        ceremony.imageTwo ||\n                        ceremony.imageThree\n                    \"\n                    class=\"cere-view-dress-cont\"\n                >\n                    <h3>Dress Code</h3>\n                    <div v-if=\"ceremony.menDress || ceremony.imageTwo\">\n                        <span>\n                            <img\n                                src=\"/assets/ceramonies/carbon_gender-male.svg\"\n                                alt=\"\"\n                            />\n                            <span>Men:</span>\n                        </span>\n                        <span v-if=\"ceremony.menDress\">\n                            {{ ceremony.menDress }}\n                        </span>\n                        <div\n                            style=\"display: flex; justify-content: center\"\n                            v-if=\"ceremony.imageTwo\"\n                        >\n                            <img\n                                class=\"dress-img\"\n                                :src=\"'/storage' + ceremony.imageTwo\"\n                                alt=\"\"\n                            />\n                        </div>\n                    </div>\n                    <div v-if=\"ceremony.ladiesDress || ceremony.imageThree\">\n                        <span>\n                            <img\n                                src=\"/assets/ceramonies/carbon_gender-female.svg\"\n                                alt=\"\"\n                            />\n                            <span>Women:</span>\n                        </span>\n                        <span v-if=\"ceremony.ladiesDress\">\n                            {{ ceremony.ladiesDress }}\n                        </span>\n                        <div\n                            style=\"display: flex; justify-content: center\"\n                            v-if=\"ceremony.imageThree\"\n                        >\n                            <img\n                                class=\"dress-img\"\n                                :src=\"'/storage' + ceremony.imageThree\"\n                                alt=\"\"\n                            />\n                        </div>\n                    </div>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\nexport default {\n    props: [\"ceremony\", \"brideName\", \"groomName\"],\n    methods: {\n        titleCase(str) {\n            let _this = this;\n            var splitStr = str.toLowerCase().split(\" \");\n            for (var i = 0; i < splitStr.length; i++) {\n                // You do not need to check if i is larger than splitStr length, as your for does that for you\n                // Assign it back to the array\n                splitStr[i] =\n                    splitStr[i].charAt(0).toUpperCase() +\n                    splitStr[i].substring(1);\n            }\n            // Directly return the joined string\n            return splitStr.join(\" \");\n        },\n    },\n};\n</script>\n\n<style scoped>\n/* .cere-popup-container {\n    background: rgba(38, 38, 38, 0.8);\n    position: fixed;\n    width: 100vw;\n    height: 100vh;\n    top: 0;\n    z-index: 999999;\n    display: flex;\n    justify-content: center;\n    overflow-y: auto;\n    -ms-overflow-style: none;\n    scrollbar-width: none;\n    padding: 10px;\n}\n.cere-popup-container::-webkit-scrollbar {\n    display: none;\n} */\n.cera-single-popup-cont {\n    background: url(/storage/guestInvitation/Peach/Background.png);\n    background-size: 100%;\n    background-position: 0 0;\n    border-radius: 8px;\n    width: 60%;\n    max-width: 512px;\n    position: relative;\n    z-index: 999999;\n    height: max-content;\n    margin: auto;\n    margin-top: 160px;\n}\n\n.cera-single-