 item\"\n            >\n                <div class=\"cer-head-mobile\">\n                    <h1>{{ titleCase(item.name) }}</h1>\n                </div>\n                <div class=\"img-peach-cont\">\n                    <img :src=\"'/storage/' + item.imageOne\" alt=\"\" />\n                </div>\n                <div class=\"peach-inner-details\">\n                    <div class=\"peach-inner-info\">\n                        <span>{{ titleCase(item.name) }}</span>\n                        <span>\n                            <img\n                                src=\"/storage/guestInvitation/Peach/calender.svg\"\n                                alt=\"\"\n                            />\n                            {{\n                                new Date(item.startDate).toLocaleDateString(\n                                    \"en-us\",\n                                    {\n                                        weekday: \"long\",\n                                        year: \"numeric\",\n                                        month: \"long\",\n                                        day: \"numeric\",\n                                    }\n                                )\n                            }}\n                            {{\n                                new Date(\n                                    \"1970-01-01T\" + item.startTime + \"Z\"\n                                ).toLocaleTimeString(\"en-US\", {\n                                    timeZone: \"UTC\",\n                                    hour12: true,\n                                    hour: \"numeric\",\n                                    minute: \"numeric\",\n                                })\n                            }}\n                        </span>\n                        <span v-for=\"e in item.venues\" :key=\"e.name\">\n                            <img\n                                src=\"/storage/guestInvitation/Peach/location.svg\"\n                                alt=\"\"\n                            />\n                            {{ e.name }}, {{ e.location.name }},\n                            {{ e.location.state }}\n                        </span>\n                    </div>\n                    <div class=\"peach-inner-dress\">\n                        <!-- <h1>Dress Code</h1> -->\n                        <div>\n                            <div>\n                                <img\n                                    src=\"/storage/guestInvitation/Peach/female.svg\"\n                                    alt=\"\"\n                                />\n                                <img\n                                    :src=\"'/storage/' + item.imageTwo\"\n                                    alt=\"\"\n                                />\n                            </div>\n                            <div>\n                                <img\n                                    src=\"/storage/guestInvitation/Peach/male.svg\"\n                                    alt=\"\"\n                                />\n                                <img\n                                    :src=\"'/storage/' + item.imageThree\"\n                                    alt=\"\"\n                                />\n                            </div>\n                        </div>\n                    </div>\n                </div>\n            </div>\n            <div\n                v-if=\"popupCera\"\n                class=\"cere-popup-container\"\n                @click=\"popupCera = null\"\n            >\n                <component\n                    @ceraClose=\"popupCera = null\"\n                    :ceremony=\"popupCera\"\n                    :brideName=\"invitation.brideName\"\n                    :groomName=\"invitation.groomName\"\n                    v-bind:is=\"ceremonyCard\"\n                ></component>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\nimport CardDefault from \"./ceremonyCards/CardDefault.vue\";\n\nexport default {\n    components: {\n        CardDefault,\n    },\n    props: [\"ceramonies\", \"invitation\"],\n    data() {\n        return {\n            popupCera: null,\n        };\n    },\n    methods: {\n        titleCase(str) {\n            let _this = this;\n            var splitStr = str.toLowerCase().split(\" \");\n            for (var i = 0; i < splitStr.length; i++) {\n                // You do not need to check if i is larger than splitStr length, as your for does that for you\n                // Assign it back to the array\n                splitStr[i] =\n                    splitStr[i].charAt(0).toUpperCase() +\n                    splitStr[i].substring(1);\n            }\n            // Directly return the joined string\n            return splitStr.join(\" \");\n        },\n    },\n    computed: {\n        ceremonyCard() {\n            //return this.popupCera.file;\n            return \"CardDefault\";\n        },\n    },\n};\n</script>\n\n<style scoped>\n.guest-invi-ceramony {\n    /* background-image: url(\"/assets/guestInvi/cera-back.png\");\n    background-repeat: no-repeat;\n    background-position: top left; */\n    /* padding: 68px;\n    display: flex; */\n    /* flex-direction: column;\n    justify-content: center;\n    align-items: center; */\n}\n/* .guest-invi-ceramony > h3 {\n    font-family: \"Perpetua\";\n    font-style: normal;\n    font-weight: 700;\n    font-size: 29.5136px;\n    line-height: 34px;\n    color: #333333;\n    margin-bottom: 15px;\n}\n.guest-invi-ceramony > h2 {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 14.5337px;\n    line-height: 22px;\n    color: #000000;\n    margin-bottom: 15px;\n    text-align: center;\n} */\n\n.cera-peach-single {\n    width: 80%;\n    background: rgba(255, 255, 255, 0.4);\n    box-shadow: 15px 20px 96px #ae924e;\n    backdrop-filter: blur(6px);\n    border-radius: 18px;\n    /* padding: 32px; */\n    padding-top: 60px;\n    display: flex;\n    margin: 20px auto;\n    border: 2px solid white;\n}\n.cera-peach-single:nth-child(even) {\n    flex-direction: row-reverse;\n}\n.cera-peach-single:nth-child(even) .img-peach-cont > img {\n    border-radius: 120px 0px 18px 0px;\n}\n\n.cera-peach-single:nth-child(even) .peach-inner-info {\n    margin-right: auto;\n    margin-left: unset;\n    clip-path: polygon(0 0, 89% 0, 100% 98%, 0% 100%);\n    padding-left: 10px;\n    padding-right: 20%;\n}\n.cera-peach-single:nth-child(even) .peach-inner-dress > div {\n    transform: skewX(17deg);\n}\n.img-peach-cont {\n    width: 50%;\n    position: relative;\n    padding-top: 30%;\n}\n.img-peach-cont > img {\n    position: absolute;\n    top: 0;\n    left: 0;\n    object-fit: cover;\n    width: 100%;\n    height: 100%;\n    border-top: 2px solid #ffffff;\n    border-right: 2px solid #ffffff;\n    border-radius: 0px 120px 0px 18px;\n}\n.peach-inner-details {\n    width: 50%;\n    padding-bottom: 15px;\n}\n.peach-inner-info {\n    width: 90%;\n    margin-bottom: 20px;\n    margin-left: auto;\n    background: rgba(255, 247, 241, 0.49);\n    box-shadow: 7px 8px 53px #eed1bd;\n    display: flex;\n    flex-direction: column;\n    padding: 25px;\n    padding-left: 21%;\n    padding-bottom: 10px;\n    clip-path: polygon(100% 0%, 100% 100%, 0% 99%, 15% 0%);\n    /* margin-top: -32px; */\n    border: 2px solid white;\n    border-right: none;\n    border-top: 1px solid white;\n}\n.peach-inner-info img {\n    margin-right: 15px;\n    filter: brightness(0) saturate(100%) invert(64%) sepia(47%) saturate(493%)\n        hue-rotate(342deg) brightness(101%) contrast(95%);\n}\n.peach-inner-info > span {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 18px;\n    line-height: 18px;\n    letter-spacing: 0.03em;\n    color: #525252;\n    margin-bottom: 20px;\n    display: flex;\n    align-items: center;\n}\n.peach-inner-info > span:nth-child(1) {\n    font-family: \"Quico Display\";\n    font-style: normal;\n    font-weight: 700;\n    font-size: 30px;\n    line-height: 30px;\n    color: #986211;\n}\n.peach-inner-dress {\n    width: 90%;\n    margin: auto;\n}\n.peach-inner-dress > h1 {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 16px;\n    line-height: 16px;\n    letter-spacing: 0.03em;\n    color: #525252;\n}\n.peach-inner-dress > div {\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: space-between;\n    background: rgba(255, 247, 241, 0.49);\n    padding: 15px;\n    transform: skewX(-17deg);\n    /* clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%); */\n}\n.peach-inner-dress > div > div {\n    width: 45%;\n    display: flex;\n    justify-content: space-around;\n    align-items: center;\n    padding: 16px;\n    border: 2px solid white;\n    background: #f8a362;\n    transform: skewX(-1deg);\n}\n.peach-inner-dress > div > div > img:nth-child(1) {\n    padding: 3px;\n    filter: invert(149%) sepia(38%) saturate(7333%) hue-rotate(178deg)\n        brightness(190%) contrast(167%);\n}\n.peach-inner-dress > div > div > img:nth-child(2) {\n    width: 83px;\n    height: 78px;\n    border: 2px solid white;\n}\n.cer-head-mobile {\n    display: none;\n    width: 100%;\n    justify-content: center;\n}\n.cer-head-mobile > h1 {\n    margin-bottom: 0;\n    width: max-content;\n    padding: 14px 30px;\n    background: #ffffff;\n    border-top-right-radius: 13px;\n    border-top-left-radius: 13px;\n    font-family: \"Reem Kufi Fun\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 40px;\n    line-height: 40px;\n    /* identical to box height, or 100% */\n\n    color: #cd8e55;\n}\n.cere-popup-container {\n    background: linear-gradient(\n        0deg,\n        rgba(95, 63, 15, 0.77),\n        rgba(95, 63, 15, 0.77)\n    );\n    position: fixed;\n    width: 100vw;\n    height: 100vh;\n    top: 0;\n    left: 0;\n    z-index: 999999;\n    display: flex;\n    justify-content: center;\n    overflow-y: auto;\n    -ms-overflow-style: none; /* IE and Edge */\n    scrollbar-width: none; /* Firefox */\n    padding: 10px;\n}\n.cere-popup-container::-webkit-scrollbar {\n    display: none;\n}\n@media screen and (max-width: 1400px) {\n    .cera-peach-single {\n        width: 90%;\n    }\n}\n@media screen and (max-width: 1200px) {\n    .peach-inner-dress > div > div {\n        padding: 8px;\n    }\n    .peach-inner-dress > div > div > img:nth-child(1) {\n        padding: 10px;\n    }\n    .cera-peach-single {\n        /* padding-right: 10px; */\n    }\n}\n@media screen and (max-width: 824px) {\n    .cera-peach-single {\n        width: 90%;\n    }\n    .peach-inner-info > span:last-child {\n        margin-bottom: 0;\n    }\n    .peach-inner-dress > div > div > img:nth-child(2) {\n        width: 65px;\n        height: 60px;\n    }\n    .peach-inner-info > span {\n        font-size: 16px;\n        line-height: 16px;\n        letter-spacing: 0.03em;\n    }\n}\n@media screen and (max-width: 767px) {\n    .cera-peach-single {\n        width: 70%;\n        flex-wrap: wrap;\n        padding-right: 32px;\n    }\n    .img-peach-cont {\n        width: 100%;\n        padding-top: 60%;\n    }\n    .peach-inner-details {\n        width: 100%;\n    }\n}\n@media screen and (max-width: 576px) {\n    .cera-peach-single {\n        width: 100%;\n        padding: 0;\n        border: none;\n        background: none;\n    }\n    .peach-inner-info {\n        position: relative;\n        z-index: 99;\n        background: rgba(255, 255, 255, 0.8);\n        border: 1.5px solid #ffffff;\n        border-bottom: none;\n        box-shadow: 4px -20px 18px rgba(0, 0, 0, 0.25);\n        backdrop-filter: blur(26.5px);\n        padding: 30px;\n        border-top-left-radius: 10px;\n        border-top-left-radius: 10px;\n        margin-bottom: 0;\n    }\n    .peach-inner-dress {\n        background: rgba(255, 255, 255, 0.8);\n        border: 1.5px solid #ffffff;\n        border-bottom-left-radius: 10px;\n        border-bottom-left-radius: 10px;\n        border-top: none;\n        box-shadow: 4px 4px 18px rgba(0, 0, 0, 0.25);\n        backdrop-filter: blur(26.5px);\n        padding: 21px;\n        padding-top: 0;\n    }\n    .peach-inner-info > span:nth-child(1) {\n        display: none;\n    }\n    .peach-inner-info > span {\n        font-size: 16px;\n        line-height: 16px;\n        letter-spacing: 0.03em;\n    }\n    .cer-head-mobile {\n        display: flex;\n    }\n}\n</style>\n"],"sourceRoot":""}]);
