 invi.brideName }}</span>\n            </div>\n            <div>\n                <img src=\"/assets/guestInvi/Frame-5602-1.png\" alt=\"\" />\n            </div>\n            <img\n                @click=\"$emit('editstory')\"\n                v-if=\"edit\"\n                class=\"edit-story\"\n                src=\"/assets/edit.svg\"\n                alt=\"\"\n            />\n        </div>\n        <div class=\"guest-story-second\">\n            <div class=\"guest-story-head-common\">\n                <h1>ONCE - UPON - A - TIME</h1>\n                <!-- <p>wedding</p> -->\n            </div>\n            <div class=\"guest-story-sec-content\">\n                <div class=\"guest-story-sec-content-single\">\n                    <div>\n                        <img\n                            :src=\"'/storage//' + ourStory.groom_image\"\n                            alt=\"\"\n                        />\n                    </div>\n                    <h3>{{ invi.groomName }}</h3>\n                    <p>\n                        {{ ourStory.groom_desc }}\n                    </p>\n                </div>\n                <div class=\"guest-story-sec-content-single\">\n                    <div>\n                        <img :src=\"'/storage/' + ourStory.bride_image\" alt=\"\" />\n                    </div>\n                    <h3>{{ invi.brideName }}</h3>\n                    <p>\n                        {{ ourStory.bride_desc }}\n                    </p>\n                </div>\n            </div>\n        </div>\n        <div class=\"guest-story-timer-cont\">\n            <!-- <div class=\"guest-story-timer-text\">\n                <span>WE ARE WAITING FOR</span>\n                The adventure\n            </div> -->\n            <div class=\"guest-banner-couple-name\">\n                <span>{{ invi.groomName }}</span>\n                <span>&</span>\n                <span>{{ invi.brideName }}</span>\n            </div>\n            <div class=\"guest-story-timer\">\n                <div class=\"guest-story-timer-single\">\n                    <span></span>\n                    <span>days</span>\n                </div>\n                <div class=\"guest-story-timer-single\">\n                    <span></span>\n                    <span>hours</span>\n                </div>\n                <div class=\"guest-story-timer-single\">\n                    <span></span>\n                    <span>minutes</span>\n                </div>\n                <div class=\"guest-story-timer-single\">\n                    <span></span>\n                    <span>seconds</span>\n                </div>\n            </div>\n        </div>\n        <div class=\"guest-story-timeline-cont\">\n            <div class=\"guest-story-head-common\">\n                <h1>OUR - LOVE STORY - TIMELINE</h1>\n                <!-- <p>wedding</p> -->\n            </div>\n            <div class=\"guest-story-timeline-all\">\n                <!-- <div class=\"guest-story-timeline-single\">\n                    <div>\n                        <img src=\"/assets/guestInvi/Rectangle196.png\" alt=\"\" />\n                    </div>\n                    <div>\n                        <h3>How we met</h3>\n                        <h5>{{ourStory.first_meet_date | moment('DD MMMM YYYY')}}</h5>\n                        <p>\n                            {{ ourStory.firstMeetDescription }}\n                        </p>\n                    </div>\n                </div> -->\n                <div class=\"guest-story-timeline-single reverse\">\n                    <div>\n                        <img\n                            :src=\"'/storage/' + ourStory.first_meet_image\"\n                            alt=\"\"\n                        />\n                    </div>\n                    <div>\n                        <div>\n                            <h3>First meet</h3>\n                            <h5>\n                                {{\n                                    ourStory.first_meet_date\n                                        | moment(\"DD MMMM YYYY\")\n                                }}\n                            </h5>\n                            <p>\n                                {{ ourStory.first_meet }}\n                            </p>\n                        </div>\n                    </div>\n                </div>\n                <div class=\"guest-story-timeline-single\">\n                    <div>\n                        <img\n                            :src=\"'/storage/' + ourStory.relation_start_image\"\n                            alt=\"\"\n                        />\n                    </div>\n                    <div>\n                        <div>\n                            <h3>The day we exchanged our hearts</h3>\n                            <h5>\n                                {{\n                                    ourStory.relation_start_date\n                                        | moment(\"DD MMMM YYYY\")\n                                }}\n                            </h5>\n                            <p>\n                                {{ ourStory.relation_start }}\n                            </p>\n                        </div>\n                    </div>\n                </div>\n                <div class=\"guest-story-timeline-single reverse\">\n                    <div>\n                        <img\n                            :src=\"'/storage/' + ourStory.marriage_decide_image\"\n                            alt=\"\"\n                        />\n                    </div>\n                    <div>\n                        <div>\n                            <h3>The day we decided to marry</h3>\n                            <h5>\n                                {{\n                                    ourStory.marriage_decide_date\n                                        | moment(\"DD MMMM YYYY\")\n                                }}\n                            </h5>\n                            <p>\n                                {{ ourStory.marriage_decide }}\n                            </p>\n                        </div>\n                    </div>\n                </div>\n            </div>\n        </div>\n        <div\n            v-show=\"ourStory.images.length > 0\"\n            class=\"gallery-cont container-lg\"\n        >\n            <h1>Gallery</h1>\n            <div id=\"guest-story-slider\" class=\"splide\">\n                <div class=\"splide__track\">\n                    <ul class=\"splide__list\" style=\"align-items: stretch\">\n                        <li\n                            v-for=\"(item, index) in ourStory.images\"\n                            :key=\"index\"\n                            style=\"border: none !important\"\n                            class=\"splide__slide\"\n                        >\n                            <div>\n                                <img :src=\"'/storage/' + item\" alt=\"\" />\n                            </div>\n                        </li>\n                    </ul>\n                </div>\n            </div>\n        </div>\n\n        <!-- <div v-show=\"ourStory.images.length > 0\" class=\"guest-story-gallery\">\n            <div class=\"guest-story-head-common\">\n                <h1>OUR - LOVE STORY - GALLERY</h1>\n                 <p>wedding</p> \n            </div>\n            <section id=\"image-single-cont\" class=\"splide\">\n                <div class=\"splide__track\">\n                    <ul class=\"splide__list\">\n                        <li\n                            v-for=\"(item, index) in ourStory.images\"\n                            :key=\"index\"\n                            class=\"splide__slide\"\n                        >\n                            <div class=\"image-single\">\n                                <img :src=\"'/storage/' + item\" alt=\"\" />\n                            </div>\n                        </li>\n                    </ul>\n                </div>\n            </section>\n        </div> -->\n    </div>\n</template>\n\n<script>\nexport default {\n    props: [\"ourStory\", \"invi\", \"edit\"],\n    data() {\n        return {\n            splider: null,\n        };\n    },\n    methods: {\n        // makeBig() {\n        //     let children = $(\"#image-single-cont ul\").children();\n        //     let index = null;\n        //     let list = [];\n        //     children.each((index, item) => {\n        //         if (item.classList.contains(\"is-visible\")) {\n        //             list.push(item);\n        //         }\n        //         item.classList.remove(\"enlarge\");\n        //     });\n\n        //     let centerIndex = Math.floor(list.length / 2);\n        //     list[centerIndex].classList.add(\"enlarge\");\n        // },\n        spliderEvent() {\n            let _this = this;\n            this.$nextTick(function () {\n                _this.splider = new Splide(`#guest-story-slider`, {\n                    gap: 5,\n                    rewind: true,\n                    pagination: false,\n                    cover: true,\n                    isNavigation: true,\n                    arrows: true,\n                    gap: 10,\n                    perPage: 3,\n                    breakpoints: {\n                        768: {\n                            perPage: 2,\n                        },\n                        567: {\n                            perPage: 1,\n                            padding: { right: \"20%\" },\n                            arrows: false,\n                            wheel: true,\n                            direction: \"ltr\",\n                            releaseWheel: false,\n                        },\n                    },\n                });\n                _this.splider.mount();\n            });\n            // this.splider.on(\"ready\", function () {\n            //     _this.makeBig();\n            // });\n            // this.splider.on(\"updated\", function () {\n            //     _this.makeBig();\n            // });\n            // this.splider.on(\"dragged\", function () {\n            //     _this.makeBig();\n            // });\n            // this.splider.on(\"moved\", function () {\n            //     _this.makeBig();\n            // });\n            // this.splider.on(\"active\", function () {\n            //     _this.makeBig();\n            // });\n            // this.splider.on(\"click\", function () {\n            //     _this.makeBig();\n            // });\n            // this.splider.on(\"visible\", function () {\n            //     _this.makeBig();\n            // });\n            this.splider.mount();\n        },\n        countDown() {\n            const fixedDate = this.invi.startDate;\n            //console.log(fixedDate);\n            const deadlineTimer = document.querySelectorAll(\n                \".guest-story-timer-single > span:nth-child(1)\"\n            );\n\n            const futureDate = new Date(fixedDate);\n\n            const futureTime = futureDate.getTime();\n\n            function getRemainingTime() {\n                let today = new Date().getTime();\n\n                let totalRemainingMS = futureTime - today;\n                if (totalRemainingMS < 0) {\n                    clearInterval(countdown);\n                    return;\n                }\n\n                //values in ms\n                const Oneday = 24 * 60 * 60 * 1000;\n                const Onehour = 60 * 60 * 1000;\n                const Oneminutes = 60 * 1000;\n\n                //remaining time calulation\n                let Day = Math.floor(totalRemainingMS / Oneday);\n                let Hour = Math.floor((totalRemainingMS % Oneday) / Onehour);\n                let Minutes = Math.floor(\n                    (totalRemainingMS % Onehour) / Oneminutes\n                );\n                let Seconds = Math.floor(\n                    (totalRemainingMS % Oneminutes) / 1000\n                );\n\n                const value = [Day, Hour, Minutes, Seconds];\n\n                function format(item) {\n                    if (item < 10) {\n                        return (item = `0${item}`);\n                    } else {\n                        return item;\n                    }\n                }\n                deadlineTimer.forEach(function (item, index) {\n                    item.innerHTML = format(value[index]);\n                });\n            }\n            let countdown = setInterval(getRemainingTime, 1000);\n            getRemainingTime();\n        },\n    },\n    mounted() {\n        this.$nextTick(function () {\n            this.countDown();\n        });\n        // document.onreadystatechange = () => {\n        //     if (document.readyState == \"complete\") {\n        //         this.spliderEvent();\n        //     }\n        // };\n    },\n};\n</script>\n\n<style scoped>\n.guest-story-cont {\n    top: 160px;\n    position: relative;\n    margin: 0 auto;\n    padding-left: 0;\n    padding-right: 0;\n    margin-bottom: 240px;\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: center;\n}\n.guest-story-cont > div:nth-child(2) {\n    padding-top: 40%;\n    position: relative;\n    width: 100%;\n    height: 100%;\n    background: rgba(255, 255, 255, 0.4);\n    backdrop-filter: blur(17px);\n    border-radius: 18px;\n}\n.guest-story-cont > div:nth-child(2) > img:nth-child(1) {\n    width: 100%;\n    height: 100%;\n    position: absolute;\n    top: 0;\n    left: 0;\n    object-fit: cover;\n    border: 3px solid #ffffff;\n    border-radius: 6px;\n    padding: 16px;\n}\n.guest-banner-couple-name {\n    margin-top: 2em;\n    text-align: center;\n    width: 100%;\n    text-align: center;\n}\n.guest-banner-couple-name > span {\n    font-family: \"Style Script\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 70px;\n    line-height: 70px;\n    text-transform: capitalize;\n    letter-spacing: 0.03em;\n    color: #04220b;\n}\n/* .guest-story-cont > img {\n    width: 100%;\n    height: 100%;\n    position: absolute;\n    top: 0;\n    object-fit: cover;\n} */\n.edit-story {\n    position: absolute;\n    top: 20px;\n    right: 20px;\n    width: 40px !important;\n    height: 40px !important;\n    z-index: 99999;\n}\n/* .guest-story-cont > .overlay {\n    background: rgba(0, 0, 0, 0.5);\n    width: 100%;\n    height: 100%;\n    position: absolute;\n    top: 0;\n    z-index: 999;\n} */\n/* .guest-story-main-img {\n    display: flex;\n    justify-content: center;\n    flex-direction: column;\n    align-items: center;\n    position: absolute;\n    z-index: 999999;\n    top: 35%;\n}\n.guest-story-main-img > div:nth-child(2) > span {\n    font-family: \"STSong\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 33.861px;\n    line-height: 44px;\n    text-transform: uppercase;\n    color: #ffffff;\n}\n.guest-story-main-img > div:nth-child(2) > span:nth-child(2) {\n    font-family: \"Stalemate\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 61.5655px;\n    line-height: 90px;\n    color: #ffffff;\n    margin: 0 15px;\n    text-transform: none;\n}\n.guest-story-main-img > div:nth-child(3) > span {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 21.0056px;\n    line-height: 32px;\n    color: #ffffff;\n} */\n.guest-story-head-common {\n    background: rgba(255, 255, 255, 0.4);\n    backdrop-filter: blur(17px);\n    border-radius: 18px;\n    width: max-content;\n    margin: auto;\n    margin-bottom: 15px;\n}\n.guest-story-head-common > h1 {\n    font-family: \"Roboto Serif 72pt SemiExpanded\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 30px;\n    line-height: 30px;\n    padding: 22px 57px;\n    letter-spacing: 0.03em;\n    color: #000000;\n    text-align: center;\n}\n/* .guest-story-head-common > p {\n    font-family: \"Stalemate\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 24.4197px;\n    line-height: 36px;\n    color: #7f004b;\n    text-align: center;\n} */\n.guest-story-second {\n    /* padding: 42px; */\n    /* background-color: #ffffff; */\n    margin-bottom: 20px;\n}\n.guest-story-sec-content {\n    display: flex;\n    width: 60%;\n    margin: auto;\n    justify-content: space-between;\n}\n.guest-story-sec-content-single {\n    width: 45%;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n}\n.guest-story-sec-content-single > div {\n    background: rgba(255, 255, 255, 0.4);\n    backdrop-filter: blur(17px);\n    border-radius: 22px;\n    position: relative;\n    padding-top: 75%;\n    width: 100%;\n    max-width: 389px;\n    max-height: 344px;\n}\n.guest-story-sec-content-single > div > img {\n    width: 100%;\n    height: 100%;\n    object-fit: cover;\n    position: absolute;\n    top: 0;\n    padding: 22px;\n    left: 0;\n    border-radius: 37px;\n}\n.guest-story-sec-content-single > h3 {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 25px;\n    line-height: 25px;\n    color: #000000;\n    text-shadow: 8px 10px 33px #ead1bb;\n    padding: 20px;\n    background: rgba(255, 255, 255, 0.93);\n    backdrop-filter: blur(3px);\n    border-radius: 13px;\n    margin-bottom: 15px;\n    margin-top: -38px;\n}\n.guest-story-sec-content-single > p {\n    font-family: \"Segoe UI\";\n    font-style: normal;\n    font-weight: 350;\n    font-size: 15.0311px;\n    line-height: 20px;\n    text-align: center;\n    color: #000000;\n    background: rgba(255, 255, 255, 0.6);\n    backdrop-filter: blur(17px);\n    border-radius: 22px;\n    padding: 10px;\n}\n.guest-story-timer-cont {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    background: rgba(255, 255, 255, 0.4);\n    backdrop-filter: blur(17px);\n    border-radius: 18px;\n    width: 80%;\n    margin: auto;\n    margin-bottom: 70px;\n    pad