        <div\n                    v-if=\"invitation.guest_invitation.logistics == 1\"\n                    :class=\"{ active: active == 'logistics' }\"\n                    @click=\"scrollToTargetAdjusted('logistics')\"\n                >\n                    <span v-if=\"screenWidth < 768\">\n                        <img src=\"/assets/guestInvi/logistics.svg\" alt=\"\" />\n                    </span>\n                    Logistics\n                </div>\n                <div\n                    :class=\"{ active: active == 'gallery' }\"\n                    @click=\"scrollToTargetAdjusted('gallery')\"\n                    v-if=\"\n                        albums.length > 0 ||\n                        galleries.length > 0 ||\n                        videos.length > 0\n                    \"\n                >\n                    <span v-if=\"screenWidth < 768\">\n                        <img src=\"/assets/guestInvi/gallery.svg\" alt=\"\" />\n                    </span>\n                    Gallery\n                </div>\n                <div\n                    :class=\"{ active: active == 'helpLine' }\"\n                    v-if=\"helpLineList.length > 0\"\n                    @click=\"scrollToTargetAdjusted('helpLine')\"\n                >\n                    <span v-if=\"screenWidth < 768\">\n                        <img src=\"/assets/invitation/helpline.svg\" alt=\"\" />\n                    </span>\n                    Help\n                </div>\n                <div\n                    v-if=\"story\"\n                    :class=\"{ active: active == 'story' }\"\n                    @click=\"scrollStory()\"\n                >\n                    <span v-if=\"screenWidth < 768\">\n                        <img src=\"/assets/guestInvi/ourstory.svg\" alt=\"\" />\n                    </span>\n                    Our Story\n                </div>\n            </div>\n        </div>\n        <div class=\"all\" :class=\"{ hide: showStory || showAllGallery }\">\n            <div ref=\"banner\" class=\"container-lg guest-banner-cont\">\n                <div class=\"guest-banner-couple-name\">\n                    <span>{{ invitation.groomName }}</span>\n                    <span>&</span>\n                    <span>{{ invitation.brideName }}</span>\n                </div>\n                <div>\n                    <div>\n                        <img src=\"/assets/guestInvi/Frame-5602-1.png\" alt=\"\" />\n                    </div>\n                </div>\n                <div class=\"guest-banner-content\">\n                    <div\n                        v-if=\"invitation.guest_invitation.inviteStatus != 6\"\n                        class=\"guest-banner-accept\"\n                    >\n                        <button\n                            :disabled=\"disableReact\"\n                            @click=\"reactInvitation('accept')\"\n                        >\n                            I’ll be there\n                        </button>\n                        <button\n                            :disabled=\"disableReact\"\n                            @click=\"reactInvitation('decline')\"\n                        >\n                            turn down\n                        </button>\n                    </div>\n                </div>\n                <div class=\"guest-banner-timer-outer\">\n                    <div class=\"guest-banner-timer\">\n                        <div class=\"guest-banner-timer-single\">\n                            <span></span>\n                            <span>days</span>\n                        </div>\n                        <div class=\"guest-banner-timer-single\">\n                            <span></span>\n                            <span>hours</span>\n                        </div>\n                        <div class=\"guest-banner-timer-single\">\n                            <span></span>\n                            <span>minutes</span>\n                        </div>\n                        <div class=\"guest-banner-timer-single\">\n                            <span></span>\n                            <span>seconds</span>\n                        </div>\n                    </div>\n                </div>\n            </div>\n            <guestCeramony\n                ref=\"ceramonies\"\n                v-if=\"ceramonyList.length > 0\"\n                :ceramonies=\"ceramonyList\"\n                :invitation=\"invitation\"\n            ></guestCeramony>\n            <guestAccommodation\n                ref=\"accomodation\"\n                v-if=\"invitation.guest_invitation.accommodation == 1\"\n                :inviStatus=\"invitation\"\n                :familyAcc=\"familyList\"\n                :user=\"loggedIn\"\n                :accomodationInfo=\"accomodationInfo\"\n                :guestAccommodationInfo=\"guestAccommodationInfo\"\n                @acc=\"(n) => (invitation = n)\"\n                @help=\"scrollToTargetAdjusted('helpLine')\"\n                style=\"padding-bottom: 97px\"\n            ></guestAccommodation>\n            <guestLogistics\n                ref=\"logistics\"\n                v-if=\"invitation.guest_invitation.logistics == 1\"\n                :inviStatus=\"invitation\"\n                :familyLog=\"familyList\"\n                :user=\"loggedIn\"\n                :departure=\"departureDetails\"\n                :arrival=\"arrivalDetails\"\n                @log=\"(n) => (invitation = n)\"\n                @help=\"scrollToTargetAdjusted('helpLine')\"\n                style=\"margin-top: -70px\"\n            ></guestLogistics>\n            <guestGallery\n                v-if=\"\n                    albums.length > 0 ||\n                    galleries.length > 0 ||\n                    videos.length > 0\n                \"\n                :inviAlbum=\"albums\"\n                :inviImages=\"galleries\"\n                :inviVideos=\"videos\"\n                :invi=\"invitation\"\n                :showAll=\"showAllGallery\"\n                @all=\"scrollGallery()\"\n                ref=\"gallery\"\n            ></guestGallery>\n            <guestHelpline\n                ref=\"helpLine\"\n                v-if=\"helpLineList.length > 0\"\n                :helpLine=\"helpLineList\"\n            ></guestHelpline>\n        </div>\n        <guestStory\n            v-if=\"story\"\n            :ourStory=\"story\"\n            :invi=\"invitation\"\n            ref=\"story\"\n            :class=\"{ hide: !showStory }\"\n            id=\"story-cont\"\n        ></guestStory>\n        <div\n            :class=\"{ hide: !showAllGallery }\"\n            class=\"gallery-all-cont wed-host-gallery container-md\"\n        >\n            <div>\n                <div\n                    @click=\"\n                        activeGallery = 'Pictures';\n                        clickedAlbum = [];\n                    \"\n                    :class=\"{\n                        'wed-host-gallery-active': activeGallery == 'Pictures',\n                    }\"\n                >\n                    Pictures\n                </div>\n                <div\n                    @click=\"\n                        activeGallery = 'Albums';\n                        clickedAlbum = [];\n                    \"\n                    :class=\"{\n                        'wed-host-gallery-active': activeGallery == 'Albums',\n                    }\"\n                >\n                    Albums\n                </div>\n                <div\n                    @click=\"\n                        activeGallery = 'Videos';\n                        clickedAlbum = [];\n                    \"\n                    :class=\"{\n                        'wed-host-gallery-active': activeGallery == 'Videos',\n                    }\"\n                >\n                    Videos\n                </div>\n            </div>\n            <div class=\"wed-host-gallery-items\">\n                <div v-if=\"activeGallery == 'Pictures'\">\n                    <div\n                        v-for=\"(item, index) in galleries\"\n                        :key=\"index\"\n                        class=\"img-cont\"\n                    >\n                        <img\n                            :src=\"\n                                '/files/' + invitation.id + '/' + item.imageName\n                            \"\n                            alt=\"\"\n                        />\n                    </div>\n                </div>\n                <div\n                    id=\"all-albums\"\n                    v-if=\"activeGallery == 'Albums' && clickedAlbum.length == 0\"\n                >\n                    <div\n                        v-for=\"(item, index) in albums\"\n                        :key=\"index\"\n                        @click=\"clickedAlbum = item.images\"\n                        class=\"album-cont\"\n                    >\n                        <span>\n                            {{ item.images.length }}\n                            <img src=\"/assets/guestInvi/bi_image-fill.svg\" />\n                        </span>\n                        <img src=\"/assets/guestInvi/Frame-5602-1.png\" alt=\"\" />\n                        <span> {{ item.name }} </span>\n                    </div>\n                </div>\n                <div v-else class=\"album-pics\">\n                    <div\n                        v-for=\"single in clickedAlbum\"\n                        :key=\"single.imageName\"\n                        class=\"img-cont\"\n                    >\n                        <img\n                            :src=\"\n                                '/files/' +\n                                invitation.id +\n                                '/' +\n                                single.imageName\n                            \"\n                            alt=\"\"\n                        />\n                    </div>\n                </div>\n                <div v-if=\"activeGallery == 'Videos'\">\n                    <div\n                        v-for=\"(item, index) in videos\"\n                        :key=\"index\"\n                        class=\"img-cont\"\n                    >\n                        <video controls>\n                            <source\n                                :src=\"\n                                    '/videos/' + invitation.id + '/' + item.name\n                                \"\n                            />\n                            Your browser does not support the video tag.\n                        </video>\n                    </div>\n                </div>\n            </div>\n        </div>\n        <flashMessage :message=\"message\"></flashMessage>\n    </div>\n</template>\n\n<script>\nimport guestCeramony from \"../invitationGold/InvitationCeramony.vue\";\nimport guestAccommodation from \"../invitationGold/InvitationAccommodation.vue\";\nimport guestLogistics from \"../invitationGold/InvitationLogistics.vue\";\nimport guestGallery from \"../invitationGold/InvitationGallery.vue\";\nimport guestHelpline from \"../invitationGold/InvitationHelpline.vue\";\nimport guestStory from \"../invitationGold/InvitationStory.vue\";\nimport flashMessage from \"../../../FlashMessage.vue\";\n\nexport default {\n    components: {\n        guestCeramony,\n        guestAccommodation,\n        guestLogistics,\n        guestGallery,\n        guestHelpline,\n        guestStory,\n        flashMessage,\n    },\n\n    props: [\n        \"guest\",\n        \"ceramonies\",\n        \"inviStatus\",\n        \"family\",\n        \"accomodationInfo\",\n        \"guestAccommodationInfo\",\n        \"arrivalDetails\",\n        \"departureDetails\",\n        \"helpLine\",\n        \"story\",\n        \"albums\",\n        \"galleries\",\n        \"videos\",\n    ],\n    data() {\n        return {\n            ceramonyList: this.ceramonies,\n            loggedIn: this.guest,\n            invitation: this.inviStatus,\n            familyList: this.family,\n            helpLineList: this.helpLine,\n            disableReact: false,\n            message: null,\n            active: \"banner\",\n            showStory: false,\n            showAllGallery: false,\n            activeGallery: \"Pictures\",\n            clickedAlbum: [],\n            screenWidth: window.screen.width,\n        };\n    },\n    methods: {\n        scrollStory() {\n            this.showStory = true;\n            this.active = \"story\";\n            // this.$refs.story.$el.scrollIntoView(true);\n            window.scrollTo(0, 0);\n        },\n        scrollGallery() {\n            this.showAllGallery = true;\n            this.active = \"gallery\";\n            // this.$refs.story.$el.scrollIntoView(true);\n            window.scrollTo(0, 0);\n        },\n        async scrollToTargetAdjusted(e) {\n            let _this = this;\n            console.log(e);\n            this.showStory = false;\n            this.showAllGallery = false;\n            await this.$nextTick();\n            // await new Promise(function (resolve, reject) {\n            //     if (_this.showAllGallery == false) {\n            //         resolve(\"resolved\");\n            //     } else {\n            //         reject(\"reject\");\n            //     }\n            // });\n            var element = null;\n            var headerOffset = 91;\n            this.active = e;\n\n            if (e == \"banner\") {\n                element = this.$refs[e];\n                headerOffset = 0;\n            } else {\n                element = this.$refs[e].$el;\n            }\n            var elementPosition = element.getBoundingClientRect().top;\n            var offsetPosition =\n                elementPosition + window.pageYOffset - headerOffset;\n            //console.log(offsetPosition, element, elementPosition);\n            window.scrollTo({\n                top: offsetPosition,\n                behavior: \"smooth\",\n            });\n        },\n        reactInvitation(type) {\n            let _this = this;\n            this.disableReact = true;\n            let link = route(\"guestaccepted\", [\n                _this.loggedIn,\n                _this.invitation,\n            ]);\n            if (type == \"decline\") {\n                link = route(\"guestdeclined\", [\n                    _this.loggedIn,\n                    _this.invitation,\n                ]);\n            }\n            axios({\n                method: \"POST\",\n                url: link,\n            })\n                .then((response) => {\n                    console.log(response);\n                    _this.disableReact = false;\n                    //_this.invitation = response.data;\n                    if (type == \"accept\") {\n                        _this.message = \"Invitation Accepted!!\";\n                        _this.invitation.guest_invitation.inviteStatus = 6;\n                    }\n                    if (type == \"decline\") {\n                        _this.message = \"Invitation Declined!!\";\n                        _this.invitation.guest_invitation.inviteStatus = 5;\n                    }\n                    console.log(_this.message);\n                    setTimeout(function () {\n                        _this.message = null;\n                    }, 3000);\n                })\n                .catch((error) => {\n                    console.log(error);\n                    _this.disableReact = false;\n                    _this.message = \"Something went wrong!!\";\n\n                    setTimeout(function () {\n                        _this.message = null;\n                    }, 3000);\n                });\n        },\n        countDown() {\n            const fixedDate = this.invitation.startDate;\n            //console.log(fixedDate);\n            const deadlineTimer = document.querySelectorAll(\n                \".guest-banner-timer-single > span:nth-child(1)\"\n            );\n\n            const futureDate = new Date(fixedDate);\n\n            const futureTime = futureDate.getTime();\n\n            function getRemainingTime() {\n                let today = new Date().getTime();\n\n                let totalRemainingMS = futureTime - today;\n                if (totalRemainingMS < 0) {\n                    clearInterval(countdown);\n                    return;\n                }\n\n                //values in ms\n                const Oneday = 24 * 60 * 60 * 1000;\n                const Onehour = 60 * 60 * 1000;\n                const Oneminutes = 60 * 1000;\n\n                //remaining time calulation\n                let Day = Math.floor(totalRemainingMS / Oneday);\n                let Hour = Math.floor((totalRemainingMS % Oneday) / Onehour);\n                let Minutes = Math.floor(\n                    (totalRemainingMS % Onehour) / Oneminutes\n                );\n                let Seconds = Math.floor(\n                    (totalRemainingMS % Oneminutes) / 1000\n                );\n\n                const value = [Day, Hour, Minutes, Seconds];\n\n                function format(item) {\n                    if (item < 10) {\n                        return (item = `0${item}`);\n                    } else {\n                        return item;\n                    }\n                }\n                deadlineTimer.forEach(function (item, index) {\n                    item.innerHTML = format(value[index]);\n                });\n            }\n            let countdown = setInterval(getRemainingTime, 1000);\n            getRemainingTime();\n        },\n        setWidth() {\n            this.screenWidth = window.screen.width;\n        },\n    },\n    mounted() {\n        window.addEventListener(\"resize\", this.setWidth);\n        this.$nextTick(() => {\n            this.countDown();\n            if (this.story) {\n                this.$refs.story.countDown();\n                this.$refs.story.spliderEvent();\n            }\n        });\n        document.onreadystatechange = () => {\n            if (document.readyState == \"complete\") {\n                this.$refs.gallery.setSplider();\n            }\n        };\n    },\n};\n</script>\n\n<style scoped>\n.guest-peach-main-cont {\n    background: url(/storage/guestInvitation/Gold/Background.png);\n    background-size: 100%;\n    background-position: 0 0;\n}\n.hide {\n    max-height: 0 !important;\n    overflow: hidden !important;\n    padding: 0 !important;\n}\n/* Navbar css start */\n.guest-invi-navbar-cont {\n    position: fixed;\n    display: flex;\n    justify-content: center;\n    top: 91px;\n    z-index: 9999999999;\n    width: 100%;\n}\n.guest-invi-navbar {\n    background: #461952;\n    border-radius: 27px;\n    padding: 8px 13px;\n    display: flex;\n    justify-content: space-between;\n    overflow-x: auto;\n    -ms-overflow-style: none; /* IE and Edge */\n    scrollbar-width: none; /* Firefox */\n}\n.guest-invi-navbar::-webkit-scrollbar {\n    display: none;\n}\n.guest-invi-navbar > div,\n.guest-invi-navbar > a {\n    text-decoration: none;\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 15px;\n    line-height: 22px;\n    text-align: center;\n    color: #9a9a9a;\n    padding: 8px 40px;\n    cursor: pointer;\n    text-decoration: none;\n    white-space: nowrap;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n}\n.guest-invi-navbar > div > span,\n.guest-invi-navbar > a > span {\n    width: 46px;\n    height: 46px;\n    margin-bottom: 25px;\n    border-radius: 50%;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    background: rgba(255, 255, 255, 0.1);\n}\n.guest-invi-navbar > .active {\n    background: #ffffff;\n    border-radius: 23px;\n    color: #461952;\n    font-weight: 700;\n}\n/* Navbar css end */\n\n/* Banner css start */\n.guest-banner-cont {\n    top: 160px;\n    position: relative;\n    margin: 0 auto;\n    padding-left: 0;\n    padding-right: 0;\n    margin-bottom: 240px;\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: center;\n}\n\n.guest-banner-cont > div:nth-child(2) {\n    padding-top: 40%;\n    position: relative;\n    width: 100%;\n    height: 100%;\n    background: rgba(255, 255, 255, 0.4);\n    backdrop-filter: blur(17px);\n    border-top-right-radius: 280px;\n    border-bottom-left-radius: 280px;\n}\n.guest-banner-cont > div:nth-child(2) > div {\n    padding: 32px;\n    position: absolute;\n    width: 100%;\n    height: 100%;\n    top: 0;\n    left: 0;\n}\n.guest-banner-cont > div:nth-child(2) > div > img:nth-child(1) {\n    width: 100%;\n    height: 100%;\n    border: 3px solid #ffffff;\n    box-shadow: 3px 11px 8px #fdc69c;\n    object-fit: cover;\n    border-radius: 6px;\n    border-radius: 280px;\n}\n\n.guest-banner-content {\n    margin: auto;\n    position: absolute;\n    top: 25%;\n    text-align: center;\n    display: flex;\n    flex-direction: column;\n    width: 100%;\n    justify-content: center;\n    align-items: center;\n    z-index: 9999;\n}\n.guest-banner-accept > button {\n    font-family: \"Segoe UI\";\n    font-style: normal;\n    font-weight: 700;\n    font-size: 13.052px;\n    line-height: 17px;\n    text-transform: uppercase;\n    padding: 13px 38px;\n}\n.guest-banner-accept > button:nth-child(1) {\n    outline: none;\n    background: rgba(196, 69, 111, 0.5);\n    border: 1.12998px solid #c4456f;\n    border-radius: 6.81176px;\n    color: #ffffff;\n    margin-right: 15px;\n}\n.guest-banner-accept > button:nth-child(2) {\n    outline: none;\n    background: rgba(72, 72, 72, 0.2);\n    border: 1.12998px solid #696969;\n    border-radius: 6.81176px;\n    color: #ffffff;\n}\n.guest-banner-couple-name {\n    margin-top: 2em;\n    text-align: center;\n    width: 100%;\n    text-align: center;\n    margin-bottom: 2em;\n}\n.guest-banner-couple-name > span {\n    font-family: \"ZCOOL XiaoWei\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 80px;\n    line-height: 80px;\n    text-align: center;\n    letter-spacing: 0.03em;\n    color: #461952;\n}\n.guest-banner-timer-outer {\n    background: rgba(255, 255, 255, 0.4);\n    backdrop-filter: blur(17px);\n    padding: 26px;\n    position: absolute;\n    bottom: 0%;\n    margin-bottom: -60px;\n    border-top-right-radius: 112px;\n    border-bottom-left-radius: 112px;\n}\n\n.guest-banner-timer {\n    display: flex;\n    margin: auto;\n    background: rgba(255, 255, 255, 0.93);\n    backdrop-filter: blur(3px);\n    padding: 30px;\n    border-top-right-radius: 78px;\n    border-bottom-left-radius: 78px;\n}\n.guest-banner-timer-single {\n    border-right: 1px solid #ffc194;\n    padding: 12px;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    margin-right: 12px;\n}\n.guest-banner-timer-single:last-child {\n    margin-right: 0;\n    border: 0 !important;\n}\n.guest-banner-timer-single > span:nth-child(1) {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 30px;\n    line-height: 30px;\n    color: #000000;\n    text-align: center;\n}\n.guest-banner-timer-single > span:nth-child(2) {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 23px;\n    line-height: 23px;\n    text-align: center;\n    letter-spacing: 0.03em;\n    color: #000000;\n}\n@media screen and (max-width: 1400px) {\n    .guest-banner-cont > div:nth-child(2) {\n        border-top-right-radius: 255px;\n        border-bottom-left-radius: 255px;\n    }\n}\n@media screen and (max-width: 1200px) {\n    .guest-banner-cont > div:nth-child(2) {\n        border-top-right-radius: 225px;\n        border-bottom-left-radius: 225px;\n    }\n}\n@media screen and (max-width: 992px) {\n    .guest-banner-cont > div:nth-child(2) {\n        padding-top: 50%;\n        margin-left: 20px;\n        margin-right: 20px;\n    }\n}\n@media screen and (max-width: 769px) {\n    .guest-banner-cont > div:nth-child(2) {\n        border-top-right-radius: 0;\n        border-bottom-left-radius: 0;\n        background: none;\n    }\n    .guest-banner-cont > div:nth-child(2) > div {\n        padding: 0;\n    }\n    .guest-banner-timer-outer {\n        border-top-right-radius: 0;\n        border-bottom-left-radius: 0;\n        background: none;\n        padding: 0;\n        margin-bottom: -10px;\n    }\n    .guest-banner-timer {\n        border-top-right-radius: 0;\n        border-bottom-left-radius: 0;\n    }\n    .guest-banner-timer-single > span:nth-child(1) {\n        font-size: 16px;\n        line-height: 16px;\n        letter-spacing: 0.03em;\n    }\n    .guest-banner-timer-single > span:nth-child(2) {\n        font-size: 12px;\n        line-height: 12px;\n        letter-spacing: 0.03em;\n    }\n    .guest-banner-timer {\n        padding: 17px 22px;\n        margin-bottom: -35px;\n    }\n\n    .wed-host-gallery {\n        padding: 14px 0 !important;\n    }\n    /* .guest-banner-cont {\n        padding-top: 100%;\n    } */\n    .guest-invi-navbar-cont {\n        top: 63px;\n    }\n    .guest-invi-navbar {\n        width: 100%;\n        max-width: 100% !important;\n        border-radius: 0 !important;\n    }\n    .guest-invi-navbar > .active {\n        background: inherit;\n        border-radius: 0;\n        color: #ffffff;\n        font-weight: 700;\n    }\n    .guest-invi-navbar > .active > span {\n        background-color: #ffffff;\n    }\n    .guest-banner-couple-name {\n        margin-top: 1px;\n    }\n    #story-cont {\n        position: relative;\n        top: 110px;\n        margin-bottom: 107px;\n    }\n    .all {\n        position: relative;\n        top: 80px;\n    }\n}\n@media screen and (max-width: 576px) {\n    .guest-banner-cont > div:nth-child(2) > div > img:nth-child(1) {\n        padding: 0px;\n    }\n    .guest-banner-cont > div:nth-child(2) {\n        margin-left: 2px;\n        margin-right: 2px;\n        padding-top: 70%;\n    }\n    .guest-banner-timer {\n        padding: 17px 3px;\n    }\n    .guest-banner-couple-name > span {\n        font-weight: 400;\n        font-size: 40px;\n        line-height: 40px;\n    }\n    .guest-banner-accept > button {\n        font-weight: 700;\n        font-size: 11.3529px;\n        line-height: 15px;\n        padding: 11px 30px;\n    }\n    .guest-banner-venue-date {\n        font-weight: 500;\n        font-size: 10px;\n        line-height: 15px;\n    }\n    .guest-invi-navbar-cont {\n        top: 56px;\n    }\n    .all {\n        position: relative;\n        top: 43px;\n    }\n    .wed-host-gallery-items > div {\n        display: grid;\n        grid-template-columns: repeat(2, 1fr) !important;\n        grid-auto-rows: max-content;\n        padding: 1em;\n        grid-column-gap: 1em;\n        grid-row-gap: 1.5em;\n    }\n}\n/* Gallery all css start */\n.wed-host-gallery {\n    background: rgba(255, 255, 255, 0.4);\n    backdrop-filter: blur(17px);\n    margin-top: 2em;\n    padding: 117px 0;\n}\n\n.wed-host-gallery > div:nth-child(1) {\n    display: flex;\n    justify-content: center;\n}\n\n.wed-host-gallery-active {\n    font-weight: bold !important;\n    color: #7f004b !important;\n    border-bottom: 4px solid #7f004b !important;\n}\n\n.wed-host-gallery > div:nth-child(1) > div {\n    font-family: Roboto;\n    font-style: normal;\n    font-weight: normal;\n    font-size: 17.8211px;\n    line-height: 21px;\n    color: #968585;\n    flex: 1;\n    text-align: center;\n    padding: 2em 0;\n    border-right: 0.891053px solid #cbc7c7;\n    border-bottom: 0.891053px solid #cbc7c7;\n    cursor: pointer;\n}\n.wed-host-gallery > div:nth-child(1) > div:last-child {\n    border-right: none;\n}\n.wed-host-gallery-items {\n    padding-bottom: 1em;\n}\n.wed-host-gallery-items > div {\n    display: grid;\n    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));\n    grid-auto-rows: max-content;\n    padding: 1em;\n    grid-column-gap: 1em;\n    grid-row-gap: 1.5em;\n}\n.img-cont,\n.album-cont {\n    position: relative;\n    padding-top: 100%;\n    border-radius: 20px;\n}\n.img-cont > img,\n.img-cont > video,\n.album-cont > img {\n    position: absolute;\n    top: 0;\n    width: 100%;\n    height: 100%;\n    object-fit: cover;\n    border-radius: 20px;\n    z-index: 99;\n}\n.album-cont > span:nth-child(1) {\n    position: absolute;\n    top: 15px;\n    right: 15px;\n    z-index: 999;\n    font-family: \"Roboto\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 16px;\n    line-height: 19px;\n    color: #ffffff;\n}\n.album-cont > span:nth-child(3) {\n    position: absolute;\n    bottom: 15px;\n    left: 15px;\n    z-index: 999;\n    font-family: \"Roboto\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 15px;\n    line-height: 18px;\n    color: #ffffff;\n}\n.album-pics {\n    /* display: none;\n    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));\n    grid-auto-rows: max-content;\n    padding: 1em;\n    grid-column-gap: 1em;\n    grid-row-gap: 1.5em; */\n}\n/* .album-pics.active {\n    display: grid !important;\n}\n.album-cont.active {\n    display: none !important;\n} */\n\n/* Gallery all css end */\n</style>\n"],"sourceRoot":""}]);
