>\n            If you are facing trouble in services contact to the help line\n            provided\n        </h6>\n        <div class=\"guest-helpline-all\">\n            <div\n                v-for=\"(item, index) in helpLine\"\n                :key=\"'helpline' + index\"\n                class=\"guest-helpline-single\"\n                @click=\"toggle($event)\"\n            >\n                <span style=\"width: 25%\">{{ item.contact_for }}</span>\n                <span style=\"width: 25%\">{{ item.member.name }}</span>\n                <span\n                    ><i class=\"fas fa-chevron-down drop-arrow\"></i>\n                    <i class=\"fas fa-chevron-up drop-arrow\"></i\n                ></span>\n                <span style=\"width: 20%\">{{ item.member.mobile }}</span>\n                <span style=\"width: 30%\">{{ item.member.email }}</span>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\nexport default {\n    props: [\"helpLine\"],\n    data() {\n        return {};\n    },\n    methods: {\n        toggle(e) {\n            //console.log(screen.width);\n            if (screen.width <= 576) {\n                let currEle = e.currentTarget;\n                let all = document.getElementsByClassName(\n                    \"guest-helpline-single\"\n                );\n                if (currEle.classList.contains(\"active\")) {\n                    currEle.classList.remove(\"active\");\n                    return;\n                }\n                // console.log(all);\n                Array.prototype.forEach.call(all, function (item) {\n                    if (item.classList.contains(\"active\")) {\n                        item.classList.remove(\"active\");\n                    }\n                });\n                // all.each(function (index, item) {\n                //     if (item.classList.contains(\"active\")) {\n                //         item.classList.remove(\"active\");\n                //     }\n                // });\n\n                currEle.classList.add(\"active\");\n            }\n        },\n    },\n};\n</script>\n\n<style scoped>\n.drop-arrow {\n    display: none;\n    margin-left: 15px;\n}\n.guest-invi-heading {\n    background: #ffffff;\n    border-radius: 35.5px;\n    margin-bottom: 15px;\n    margin-top: -102px;\n    padding: 20px 40px;\n    width: max-content;\n    font-family: \"Roboto Serif 72pt SemiExpanded\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 30px;\n    line-height: 30px;\n    letter-spacing: 0.03em;\n    color: #000000;\n    text-shadow: 8px 10px 33px #ead1bb;\n}\n.guest-invi-sub-heading {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 16px;\n    line-height: 16px;\n    /* identical to box height, or 100% */\n\n    letter-spacing: 0.03em;\n\n    color: #000000;\n    margin-bottom: 15px;\n    text-align: center;\n}\n.guest-inv-helpline {\n    background: rgba(255, 255, 255, 0.4);\n    backdrop-filter: blur(17px);\n    border-radius: 18px;\n    margin-top: 100px;\n    padding: 67px 15px;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n}\n.guest-helpline-all {\n    width: 90%;\n    margin: 10px 0;\n}\n.guest-helpline-single {\n    background: #ffffff;\n    border-radius: 87px;\n    padding: 22px 23px;\n    width: 100%;\n    display: flex;\n    margin-bottom: 10px;\n    align-items: center;\n}\n.guest-helpline-single > span {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 12px;\n    line-height: 16px;\n    letter-spacing: 0.03em;\n    display: block;\n    color: #000000;\n    white-space: nowrap;\n    text-overflow: ellipsis;\n    overflow-x: hidden;\n    margin-right: 5px;\n}\n.guest-helpline-single > span:nth-child(1) {\n    font-weight: 500;\n    font-size: 16px;\n    line-height: 24px;\n    color: #c4854c;\n}\n@media screen and (max-width: 968px) {\n    .guest-helpline-all {\n        width: 100% !important;\n    }\n}\n@media screen and (max-width: 576px) {\n    .guest-helpline-all {\n        width: 100% !important;\n    }\n    .guest-helpline-single .fa-chevron-down.drop-arrow {\n        display: block;\n    }\n    .guest-helpline-single .fa-chevron-up.up-arrow {\n        display: none;\n    }\n    .guest-helpline-single.active .fa-chevron-down.drop-arrow {\n        display: none;\n    }\n    .guest-helpline-single.active .fa-chevron-up.drop-arrow {\n        display: block;\n    }\n    .guest-helpline-single {\n        flex-wrap: wrap;\n    }\n    .guest-helpline-single > span:nth-child(1) {\n        width: 40% !important;\n    }\n    .guest-helpline-single > span:nth-child(2) {\n        width: 40% !important;\n    }\n    .guest-helpline-single > span:nth-child(3) {\n        width: max-content !important;\n        text-align: right;\n        margin-left: auto;\n    }\n    .guest-helpline-single > span:nth-child(4) {\n        width: 100% !important;\n        display: none;\n    }\n    .guest-helpline-single > span:nth-child(5) {\n        width: 100% !important;\n        display: none;\n    }\n    .guest-helpline-single.active > span:nth-child(4),\n    .guest-helpline-single.active > span:nth-child(5) {\n        display: block !important;\n        margin-bottom: 5px;\n    }\n    .guest-helpline-single.active > span:nth-child(1) {\n        margin-bottom: 10px;\n    }\n}\n</style>\n"],"sourceRoot":""}]);
